<template>
    <main class="hide-overflow">
        <div class="auth">
            <form>
                <h1>Accès admin</h1>
                <!-- <label for="name">Name:</label> -->
                <input type="text" v-model="name" id="name" placeholder="Name" @keyup.enter="postLogin">
                <!-- <br> -->
                <!-- <label for="password">Password:</label> -->
                <input :type="showPwd?'text':'password'" v-model="password" id="password" placeholder="Password" @keyup.enter="postLogin">
                <sup style="cursor:pointer; text-align:right; margin-right:5px" @click="showPwd = !showPwd">{{showPwd?'Cacher le mot de passe':'Afficher le mot de passe'}}</sup>
                <br>
                <input type="button" value="Login" @click="postLogin" >
                <BarLoader :height=2 :width=100 v-show="loading" style="margin: 0 auto; border-radius:5px, display: none"></BarLoader>
                <sup style="text-align:center"><a href="/register">Créer un compte</a></sup>
            </form>
        </div>
    </main>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import axios from 'axios'


export default {
    data () {
        return {
            name: '',
            showPwd: false,
            password: '',
            loading: false
        }
    },
    computed: {
        ...mapGetters('jwt',[
            'jwt',
            'jwtSubject',
            'jwtIssuer'
        ]),
        ...mapGetters('api', [
            'apiPath'
        ])
    },
    methods: {
        ...mapActions('jwt',[
            'setJwt',
            'setLevel',
            'checkJwt'
        ]),
        ...mapActions('alert',[
            'newAlert'
        ]),
        // Pushes posts to the server when called.
        postLogin () {
            this.loading = true
        axios({
            method: 'post',
            url: `${this.apiPath}/api/login`,
            data: {
                name: this.name,
                password: this.password
            }
        })
            .then(response => {
            this.loading = false
            if (response.data.status == 'success') {
                this.newAlert({type:'success', content:'Logged'})
            }
            if (response.data.token) {
                this.setJwt(response.data.token)
                this.setLevel(response.data.user.level)
                sessionStorage.setItem('tokenSession', response.data.token);
                sessionStorage.setItem('level', response.data.user.level);
                this.$router.push(this.$route.query.after? this.$route.query.after:'/')
            }
            if (response.data.status == 'error') {
                let error = response.data.error.password ? "Password field is empty": (response.data.error.name ? "Name field is empty":response.data.error)
                this.newAlert({type:'error', content:error})
            }
            })
            .catch(e => {
                this.loading = false
                if (e.response.status == 403) {
                    this.newAlert({type:'error', content:"Votre compte n'étant pas validé, vous ne pouvez pas accéder à l'interface d'administration"})
                } else {
                    this.newAlert({type:'error', content:e})
                }
                
            })
        }
    },
    created () {
        if (sessionStorage.getItem("tokenSession") != null) {
            this.checkJwt(sessionStorage.getItem("tokenSession"))
            .then(() => {
                this.setLevel(sessionStorage.getItem("level"))
                this.newAlert({type:'success', content:'Automatically logged'})
                this.$router.push(this.$route.query.after? this.$route.query.after:'/')
            })
        }
        window.scrollTo(0, 0)
    }
}
</script>
